import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { Col, CustomInput, FormGroup, Row } from 'reactstrap';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../../dependencies/empresaMLFDependency';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../../models/dtos/ger/printerDataBasic';
import { DATES } from '../../../../../../models/enumerated/ger/printerConfigTypeEnum';
import { UF_ENUM_VALUES } from '../../../../../../models/enumerated/ger/ufEnum';
import { ORIGEM_LANCAMENTO_MLF_ENUM_VALUES } from '../../../../../../models/enumerated/mlf/origemLancamentoMLFEnum';
import { Reducers } from '../../../../../../store/ducks';
import { GlobalState } from '../../../../../../store/ducks/global.duck';
import ReportFormView from '../../../../report/ReportFormView';
import { CLASS_KEY, RelacaoEntradaSaidaRV } from '../relacaoEntradaSaidaRV';

export const REPORT_EVENT: string = 'printRelacaoSaidaEstado';
export const REPORT_TITLE = 'Relação de Saídas Por Estado (UF)';

const BASIC_DATA: PrinterDataBasic = {
  ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY, DATES.value),
  dtFinal: '31/12/2017',
  dtInicial: '01/12/2017',
};

export default function RelacaoSaidaEstadoReportView(props: WindowComponentProps) {
  const history = useHistory();

  const formProps = useForm<RelacaoEntradaSaidaRV>({ mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa !== null) {
        const id = globalReducer.globalParameter.empresa.id;
        setTimeout(() => {
          formProps.setValue('idGerEmpresa', id);
          formProps.setValue('uf', null);
          formProps.triggerValidation('uf');
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      {...props}
      title={REPORT_TITLE}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <ReportFormView formProps={formProps} printerData={BASIC_DATA} history={history}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              id="origemLancamentoInt"
              label="Origem do Lançamento"
              isPrependLabel={true}
              placeholder="Informe a Origem do Lançamento"
              formProps={formProps}
              inputDropDownProps={{
                optionsProvider: ORIGEM_LANCAMENTO_MLF_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="uf"
              label="Filtrar por"
              isPrependLabel={true}
              placeholder="Informe o Estado para o filtro"
              formProps={formProps}
              inputDropDownProps={{
                optionsProvider: UF_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="subtotalDiario" innerRef={formProps.register} label="Exibir Subtotais Por Dia" name="subtotalDiario" type="switch" />
          </Col>
        </FormGroup>
      </ReportFormView>
    </WindowComponent>
  );
}
